body {
    background-color: steelblue;
}

.box {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 15px;
    margin: 15px 10px;
    background-color: black;
    color: white;
    border-radius: 10px;
    position: relative;
    font-style: italic;
    font-family: sans-serif;
}


.box:hover {
    background-color: white;
    color: black;
    /* background-color: black; */

}

@media(max-width:550px) {
    .box {
        width: 46%
    }
}


.poster {
    border-radius: 10px;
}

.title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
    font-style: italic;
}

.subtitle {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
    margin: auto;
    padding-bottom: 3px;
    font-style: italic;
}